@import '~antd/dist/antd.css';
.App {
  text-align: center;
}

.head{
  color: white;
  font-size: 30px;
}

.head:hover{
  cursor: pointer;
}

body{
  background-color: #666699;
  font-family: 'Arvo', serif;
}

.ant-btn, .ant-input{
  height: 50px;
  font-size: 25px;
  color: #001529;
}

.ant-btn{
  background-color: #9999cc;
  border: none;
}

.ant-card{
  margin: 20px auto;
}

.row{
  width: 100%;
}

.ant-card-head-title{
  font-size: 20px;
}

.ant-descriptions-title{
  font-size: 30px;
}